import React from "react"
import { graphql, Link, StaticQuery } from "gatsby"

import Contact from "../../components/Contact"
import Navbar from "../../components/Navbar"
import ProjectsTile from "../../components/widgets/ProjectsTile"
import ScrollButton from "../../components/widgets/backToTop"
import Seo from "../../components/SEO"

import * as projectsStyles from "../../styles/projects.module.css"
import "../../styles/global.css"

const query = graphql`
  query ProjectQuery {
    dataJson(title: { eq: "Projects" }) {
      projects {
        name
        desc
        url
        img {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
        techs {
          name
          url
          img {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        urls {
          name
          url
          img {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`

export default function Projects() {
  return (
    <StaticQuery
      query={query}
      render={data => (
        <React.Fragment>
          <Seo title="Projects" />
          <div className="nav">
            <Navbar></Navbar>
          </div>
          <div className="notNav">
            <div className="container">
              <div className={projectsStyles.divider}></div>
              <div className={projectsStyles.title}>
                PROJECTS
                <div className={projectsStyles.desc}>PERSONAL PROJECTS</div>
              </div>
              <div className={`row ${projectsStyles.row}`}>
                {data.dataJson.projects.map(project => (
                  <ProjectsTile
                    name={project.name}
                    desc={project.desc}
                    url={project.url}
                    fluid={project.img.childImageSharp.fluid}
                    techStack={project.techs}
                    urls={project.urls}
                  />
                ))}
              </div>
              <div className={`row ${projectsStyles.moreProjectsDiv}`}>
                <Link className={`${projectsStyles.moreProjects}`} to="/">
                  {" "}
                  &lt; Go Back &gt;
                </Link>
              </div>
            </div>
            <Contact></Contact>
            <ScrollButton></ScrollButton>
          </div>
        </React.Fragment>
      )}
    />
  )
}
